s overlaid). I used LIRA (see below) to investigate these X-ray structural differences in about 100 X-ray jets and showed these \"positional offsets\" are ubiquitous and that multi-zone models must be adopted to explain the X-rays. Based on inter-knot distances, if the discrete features in these jets (called knots) are to be produced by an intermittent activity in the central engine, as suggested by previous authors, I showed that knots can at most be mildly-relativistic. This slow speed questions the application of one-zone iC/CMB models which require highly relativistic jets. Building up on the previous suggestions that the knots in these jets are slow-moving parts of the jet embedded in an faster and lighter outer-flow, we suggested that the shock-accelerated plasma flows around the obstacle to create the observed X-ray/radio offsets and morphology. ")]),_c('v-card-actions',[_c('v-spacer'),_c('v-tooltip',{attrs:{"bottom":""},scopedSlots:_vm._u([{key:"activator",fn:function(ref){
